"use client";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { RefreshCw } from "lucide-react";

export default function Navbar({ onRefresh }) {
  const pathname = usePathname();

  const links = [
    { href: "/", label: "Home" },
    { href: "/jobs", label: "Jobs" },
    { href: "/provider/dashboard", label: "Provider" },
    { href: "/admin/approve", label: "Admin" },
  ];

  return (
    <nav className="w-full bg-white shadow-sm border-b border-orange-100">
      <div className="max-w-6xl mx-auto flex items-center justify-between px-6 py-3">
        {/* Logo */}
        <Link
          href="/"
          className="text-2xl font-bold tracking-tight flex items-baseline"
        >
          <span className="text-orange-600">Khedme</span>
          <span className="text-black ml-1 text-[1.6rem] font-extrabold leading-none">
            بال
          </span>
        </Link>

        {/* Links */}
        <div className="flex items-center space-x-6">
          {links.map(({ href, label }) => {
            const active = pathname === href;
            return (
              <Link
                key={href}
                href={href}
                className={`font-medium ${
                  active
                    ? "text-orange-600 border-b-2 border-orange-600 pb-1"
                    : "text-gray-700 hover:text-orange-600"
                } transition-colors`}
              >
                {label}
              </Link>
            );
          })}

          {/* Refresh Button (optional) */}
          {onRefresh && (
            <Button
              onClick={onRefresh}
              className="ml-3 bg-orange-500 hover:bg-orange-600 text-white flex items-center gap-1"
            >
              <RefreshCw size={16} />
              Refresh
            </Button>
          )}
        </div>
      </div>
    </nav>
  );
}
